import styled from "styled-components";

export const ButtonCmn = styled.button`
  /* Adapt the colors based on primary prop */
  background: ${(props) => (props.primary ? "palevioletred" : "white")};
  color: ${(props) => (props.primary ? "white" : "palevioletred")};
  font-size: 1em;
  /* margin: 0 1em; */
  padding: 0.25em 1em;
  border: 2px solid palevioletred;
  border-radius: 8px;
  &:hover {
    background: ${(props) => (props.primary ? "white" : "palevioletred")};
    color: ${(props) => (props.primary ? "palevioletred" : "white")};
  }
`;

export const TomatoButton = styled(ButtonCmn)`
  background: ${(props) => (props.tomato ? "white" : "tomato")};
  color: ${(props) => (props.tomato ? "tomato" : "white")};
  border-color: tomato;

  &:hover {
    background: ${(props) => (props.tomato ? "tomato" : "white")};
    color: ${(props) => (props.tomato ? "white" : "tomato")};
    border-color: ${(props) => (props.tomato ? "white" : "tomato")};
  }
`;

export const Yellowbutton = styled(ButtonCmn)`
  background: #022115;
  border: solid 1px #022115;
  &:hover {
    border: 1px solid #0000;
    background: linear-gradient(#022115, #022115) padding-box,
      linear-gradient(109.07deg, #fee1af 17.41%, #c79e4b 96.23%) border-box;
  }
`;
export const Buybutton = styled(ButtonCmn)`
  background: linear-gradient(109.07deg, #fee1af 17.41%, #c79e4b 96.23%);
  color: #082a1a;
  border: solid 1px #fee1af;
  &:hover {
    border-color: #022115;
    background: linear-gradient(109.07deg, #fee1af 17.41%, #c79e4b 96.23%);
    color: #082a1a;
  }
`;
