import React from "react";

function ListsandKey() {
  const data = [
    { id: 1, name: "John Doe" },
    { id: 2, name: "Victor Wayne" },
    { id: 3, name: "Jane Doe" },
  ];
  return (
    <div>
      <div className="users">
        {data.map((user) => (
          <div key={user.id} className="user">
            {user.name}
          </div>
        ))}
      </div>
    </div>
  );
}

export default ListsandKey;
